import React, { useState } from 'react';
import PropTypes from 'prop-types';

import shortid from 'shortid';
import s from './ContactForm.module.css';
import AddIcon from '@material-ui/icons/Add';
import CancelIcon from '@material-ui/icons/Cancel';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import Button from '@material-ui/core/Button';

const initialState = {
  id: shortid.generate(),
  name: '',
  phoneNumber: '',
  isOpen: false,
  show: false,
};

const ContactForm = ({
  contacts,
  onSubmit,
  showInput,
  onChangeShow,
  onChangeInput,
}) => {
  const [state, setState] = useState(initialState);
  const { name, phoneNumber, isOpen, show } = state;

  const handleSubmit = e => {
    e.preventDefault();

    const newContact = {
      id: shortid.generate(),
      name,
      phoneNumber,
    };

    const checkName = contacts.find(
      contact => contact.name === newContact.name,
    );

    if (checkName) {
      alert(`Contact witn name ${name} already exists`);
      setState(initialState);
      onChangeShow();
      return;
    }
    onSubmit(newContact);
    setState(initialState);
    onChangeShow();
  };

  const handleChange = e => {
    setState(prev => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const toggleModal = () => {
    if (showInput) {
      setState({ show: false });
    }
    setState({
      id: shortid.generate(),
      name: '',
      phoneNumber: '',
      isOpen: !isOpen,
      show: !show,
    });
    onChangeShow();
  };

  return (
    <>
      <Button
        className={s.btn}
        onClick={!showInput || show ? toggleModal : onChangeInput}
        color="primary"
        variant="outlined"
      >
        <AddIcon className={s.icon} />
      </Button>

      <div className={s.modal}>
        <div
          className={isOpen ? [s.modalBody, s.active].join(' ') : s.modalBody}
        >
          <div>
            <form onSubmit={handleSubmit} className={s.form}>
              <label className={s.label}>
                <input
                  className={s.input}
                  placeholder="Name"
                  type="text"
                  name="name"
                  value={name}
                  pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
                  title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
                  required
                  onChange={handleChange}
                />
              </label>

              <label>
                <input
                  className={s.input}
                  type="tel"
                  name="phoneNumber"
                  placeholder="Phone number"
                  value={phoneNumber}
                  pattern="(\+?( |-|\.)?\d{1,2}( |-|\.)?)?(\(?\d{3}\)?|\d{3})( |-|\.)?(\d{3}( |-|\.)?\d{4})"
                  title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å –∏–∑ 11-12 —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ü–∏—Ñ—Ä—ã, –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –ø—É–∑–∞—Ç—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
                  required
                  onChange={handleChange}
                />
              </label>
              <div className={s.buttons}>
                <Button
                  color="primary"
                  variant="outlined"
                  className={s.btn}
                  type="submit"
                >
                  <PersonAddIcon className={s.icon} />
                </Button>
                <Button
                  color="primary"
                  variant="outlined"
                  className={s.btn}
                  onClick={toggleModal}
                >
                  <CancelIcon className={s.icon} />
                </Button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

ContactForm.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  contacts: PropTypes.array.isRequired,
};

export default ContactForm;
